___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --pGray: #495460;
    --titleGray: #2C2F24;
    --btnsHeroMenu: #182226;
    --pricerRed: #AD343E;
}

#menuHead {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#heroMenu {
    margin-top: 140px;
    gap: 90px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#heroMenu #menuText {
    text-align: center;
    width: 500px;
} 

#heroMenu #menuText h1 {
    font-size: 96px;
    font-family: 'Playfair Display';
    font-weight: 400;
    margin: 0;
    color: var(--titleGray);
}

#heroMenu #menuText p {
    color: var(--pGray);
    margin: 0;
    margin-bottom: 40px;
}

#heroMenu #menuBtns {
    display: flex;
    gap: 30px;
    flex-wrap: wrap
}

#heroMenu #menuBtns a {
    background-color: transparent;
    padding: 10px 40px;
    border-radius: 50px;
    border: 1px solid var(--btnsHeroMenu);
    transition: .15s ease-in-out;
}

#heroMenu #menuBtns a:hover {
    color: white;
    background-color: var(--btnHeroRed);
    border-color: transparent;
}

#menuCards {
    width: 80%;
    gap: 20px;
    display: grid;
    justify-items: center;
    margin-bottom: 30px;

    grid-template-columns: repeat(auto-fit , minmax(306px, 1fr));
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 20px;
    background-color: white;
    box-shadow: 0px 0px 11px 4px #0000002e;
}

.card:hover {
    scale: 1.05;
    z-index: 1;
}

.card, .card img {
    width: 100%;
}

.card .dishPrice {
    margin: 15px 0;
    font-size: 1.4rem;
    font-weight: bold;
    color: var(--pricerRed);
}

.card span:last-of-type {
    font-size: 1.2rem;
    color: var(--titleGray);
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/styles/menu.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,oBAAoB;IACpB,uBAAuB;IACvB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,+BAA+B;IAC/B,gBAAgB;IAChB,SAAS;IACT,uBAAuB;AAC3B;;AAEA;IACI,mBAAmB;IACnB,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT;AACJ;;AAEA;IACI,6BAA6B;IAC7B,kBAAkB;IAClB,mBAAmB;IACnB,qCAAqC;IACrC,4BAA4B;AAChC;;AAEA;IACI,YAAY;IACZ,mCAAmC;IACnC,yBAAyB;AAC7B;;AAEA;IACI,UAAU;IACV,SAAS;IACT,aAAa;IACb,qBAAqB;IACrB,mBAAmB;;IAEnB,4DAA4D;AAChE;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;IACnB,uBAAuB;IACvB,sCAAsC;AAC1C;;AAEA;IACI,WAAW;IACX,UAAU;AACd;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,iBAAiB;IACjB,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;IACjB,uBAAuB;IACvB,iBAAiB;AACrB","sourcesContent":[":root {\n    --pGray: #495460;\n    --titleGray: #2C2F24;\n    --btnsHeroMenu: #182226;\n    --pricerRed: #AD343E;\n}\n\n#menuHead {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n#heroMenu {\n    margin-top: 140px;\n    gap: 90px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n#heroMenu #menuText {\n    text-align: center;\n    width: 500px;\n} \n\n#heroMenu #menuText h1 {\n    font-size: 96px;\n    font-family: 'Playfair Display';\n    font-weight: 400;\n    margin: 0;\n    color: var(--titleGray);\n}\n\n#heroMenu #menuText p {\n    color: var(--pGray);\n    margin: 0;\n    margin-bottom: 40px;\n}\n\n#heroMenu #menuBtns {\n    display: flex;\n    gap: 30px;\n    flex-wrap: wrap\n}\n\n#heroMenu #menuBtns a {\n    background-color: transparent;\n    padding: 10px 40px;\n    border-radius: 50px;\n    border: 1px solid var(--btnsHeroMenu);\n    transition: .15s ease-in-out;\n}\n\n#heroMenu #menuBtns a:hover {\n    color: wh