___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --headerGray: #474747;
    --btnDarkGray: #182226;
    --btnMenu: #DBDFD0;
    --textHeroGray: #2C2F24;
    --btnHeroRed: #AD343E;
}

body {
    padding: 0;
    margin: 0;
    height: 100%;
    font-family: 'DM Sans';
}

* {
    box-sizing: border-box;
}

#home {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: center;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: white;
    border-bottom: 1px solid var(--textHeroGray);
    z-index: 2;
}

ul, a {
    list-style: none;
    text-decoration: none;
    color: black;
    padding: 0;
    margin: 0;
}

#top-header {
    display: flex;
    background-color: var(--headerGray);
    justify-content: space-between;
    padding: 0 110px;
    height: 45px;
}

#top-header li {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
}

#top-header ul {
    display: flex;
    gap: 25px;
    color: white;
    justify-content: center;
}
 
#info > li img {
    height: 11px;
}

#info > li:first-child img {
    height: 15px;
}

#top-header > #social {
    gap: 2px;
}

#logo-head {
    display: flex;  
    text-wrap: nowrap;
}

#logo-head img {
    height: 46px;
}

#logo-head h1 {
    font-family: 'Playfair Display';
    font-weight: bold;
    font-style: italic;
    font-size: 35px;
    color: var(--headerGray);
    margin: 0;
}

#head-menu {
    display: flex;
    font-size: 15px;
    gap: 3px;
}

#head-menu li a {
    background-color: transparent;
    padding: 6px 18px;
    border-radius: 50px;
    transition: .15s ease-in-out;
}

#head-menu li a:hover {
    background-color: var(--btnMenu);
}

#bottom-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 105px;
}

.btn a {
    color: var(--btnDarkGray);
    font-weight: bold;
    display: flex;
    align-items: center;
    height: 38px;
    padding: 0 16px;
    border-radius: 50px;
    border: 1px solid var(--btnDarkGray);
    background-color: transparent;
    text-wrap: nowrap;
    transition: .15s ease-in-out;
}

.btn a:hover {
    background-color: var(--btnDarkGray);
    color: white;
}

#hero {
    width: 500px;
    text-align: center;
    color: var(--textHeroGray);
    margin-top: 100px;
}

#hero h1 {
    font-family: 'Playfair Display';
    font-size: 85px;
    font-weight: 400;
    margin: 0;
    padding: 0;
    line-height: 82px;
}

#hero p {
    margin: 0;
    margin-top: 30px;
    font-size: 19px;
}

#hero-btns {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    gap: 15px;
}

#hero-btns div a {
    padding: 27px 17px;
}

#hero-btns div a:hover  {
    background-color: var(--btnHeroRed);
    border-color: transparent;
}`, "",{"version":3,"sources":["webpack://./src/styles/home.css"],"names":[],"mappings":"AAEA;IACI,qBAAqB;IACrB,sBAAsB;IACtB,kBAAkB;IAClB,uBAAuB;IACvB,qBAAqB;AACzB;;AAEA;IACI,UAAU;IACV,SAAS;IACT,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,yDAAgD;IAChD,sBAAsB;IACtB,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,2BAA2B;AAC/B;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,uBAAuB;IACvB,4CAA4C;IAC5C,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;IACrB,YAAY;IACZ,UAAU;IACV,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mCAAmC;IACnC,8BAA8B;IAC9B,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,+BAA+B;IAC/B,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,wBAAwB;IACxB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,eAAe;IACf,QAAQ;AACZ;;AAEA;IACI,6BAA6B;IAC7B,iBAAiB;IACjB,mBAAmB;IACnB,4BAA4B;AAChC;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,mBAAmB;IACnB,oCAAoC;IACpC,6BAA6B;IAC7B,iBAAiB;IACjB,4BAA4B;AAChC;;AAEA;IACI,oCAAoC;IACpC,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,0BAA0B;IAC1B,iBAAiB;AACrB;;AAEA;IACI,+BAA+B;IAC/B,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;IACV,iBAAiB;AACrB;;AAEA;IACI,SAAS;IACT,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,mCAAmC;IACnC,yBAAyB;AAC7B","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100;0,9..40,200;0,9..40,300;0,9..40,400;0,9..40,500;0,9..40,600;0,9..40,700;0,9..40,800;0,9..40,900;0,9..40,1000;1,9..40,100;1,9..40,200;1,9..40,300;1,9..40,400;1,9..40,500;1,9..40,600;1,9..40,700;1,9..40,800;1,9..40,900;1,9..40,1000&family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800