___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --infoCardGray: #474747;
    --heroTextTop: #2C2F24;
    --hetoLowerText: #414536;
}

#heroAbout {
    height: 88vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 120px;
    flex-wrap: wrap;
}

#cardAbout {
    position: relative;
    margin-top: 170px;
}

#cardAbout > img {
    width: 450px;
}

#cardAboutInfo {
    position: absolute;
    background-color: var(--infoCardGray);
    border-radius: 10px;
    color: white;
    width: 320px;
    padding: 37px 30px;
    top: 200px;
    left: 150px;
    font-size: 13px;
}

#cardAboutInfo h2 {
    margin: 0;
    margin-bottom: 30px;
}

#cardAboutInfo ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: white;
}

#cardAboutInfo ul > li {
    display: flex;
}

#cardAboutInfo ul > li img {
    max-width: 25px;
    margin-right: 10px;
}

#cardAboutInfo ul > li:not(:last-child) img {
    width: 15px;
}

#cardAboutInfo ul > li:last-child img {
    width: 18px;
}

#cardAboutInfo ul > li:nth-child(3) img {
    margin-right: 8px;
}


#heroTextAbout {
    width: 600px;
    color: var(--heroTextTop);
}

#heroTextAbout h1 {
    font-family: 'Playfair Display';
    font-size: 50px;
    font-weight: 600;
    margin: 0;
}

#heroTextAbout p:last-child {
    color: var(--hetoLowerText);
}`, "",{"version":3,"sources":["webpack://./src/styles/about.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,sBAAsB;IACtB,wBAAwB;AAC5B;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,qCAAqC;IACrC,mBAAmB;IACnB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;AACrB;;;AAGA;IACI,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,+BAA+B;IAC/B,eAAe;IACf,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,2BAA2B;AAC/B","sourcesContent":[":root {\n    --infoCardGray: #474747;\n    --heroTextTop: #2C2F24;\n    --hetoLowerText: #414536;\n}\n\n#heroAbout {\n    height: 88vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 120px;\n    flex-wrap: wrap;\n}\n\n#cardAbout {\n    position: relative;\n    margin-top: 170px;\n}\n\n#cardAbout > img {\n    width: 450px;\n}\n\n#cardAboutInfo {\n    position: absolute;\n    background-color: var(--infoCardGray);\n    border-radius: 10px;\n    color: white;\n    width: 320px;\n    padding: 37px 30px;\n    top: 200px;\n    left: 150px;\n    font-size: 13px;\n}\n\n#cardAboutInfo h2 {\n    margin: 0;\n    margin-bottom: 30px;\n}\n\n#cardAboutInfo ul {\n    padding: 0;\n    margin: 0;\n    display: flex;\n    flex-direction: column;\n    gap: 30px;\n    color: white;\n}\n\n#cardAboutInfo ul > li {\n    display: flex;\n}\n\n#cardAboutInfo ul > li img {\n    max-width: 25px;\n    margin-right: 10px;\n}\n\n#cardAboutInfo ul > li:not(:last-child) img {\n    width: 15px;\n}\n\n#cardAboutInfo ul > li:last-child img {\n    width: 18px;\n}\n\n#cardAboutInfo ul > li:nth-child(3) img {\n    margin-right: 8px;\n}\n\n\n#heroTextAbout {\n    width: 600px;\n    color: var(--heroTextTop);\n}\n\n#heroTextA